import React, { useState, useEffect } from "react";
import ReactPaginate from "react-paginate";
import { Link } from "react-router-dom";

function Films() {
  const [data, setData] = useState([]);

  const fetchData = async () => {
    const response = await fetch(
      `https://api.themoviedb.org/3/movie/popular?api_key=${process.env.REACT_APP_API_KEY}&page=1`
    );
    const data = await response.json();
    setData(data.results);
    console.log(data);
  };

  useEffect(() => {
    fetchData();
  }, []);

  const fetchPagination = async (selected) => {
    const response = await fetch(
      `https://api.themoviedb.org/3/movie/popular?api_key=${process.env.REACT_APP_API_KEY}&page=${selected}`
    );
    const page = await response.json();
    return page.results;
  };

  const handlePageClick = async (click) => {
    console.log(click.selected);

    let selected = click.selected + 1;

    const fetchFromPagination = await fetchPagination(selected);

    setData(fetchFromPagination);
  };

  return (
    <div className="films">
      {data.map((item) => {
        return (
          <Link key={item.id} to={`/filmpage/` + item.id}>
            <div className="film">
              <div className="film-img">
                <img
                  src={"https://image.tmdb.org/t/p/w500" + item.poster_path}
                  alt="Poster"
                />
              </div>
              <h1 className="title">{item.title}</h1>
              <span>{item.vote_average}</span>
            </div>
          </Link>
        );
      })}
      <div className="pagi">
        <ReactPaginate
          breakLabel="..."
          nextLabel="next >"
          onPageChange={handlePageClick}
          pageRangeDisplayed={3}
          pageCount={450}
          previousLabel="< previous"
          renderOnZeroPageCount={null}
          containerClassName={"pagination"}
          pageClassName={"page-item"}
          pageLinkClassName={"page-link"}
          previousClassName={"page-item"}
          previousLinkClassName={"page-link"}
          nextClassName={"page-item"}
          nextLinkClassName={"page-link"}
          breakClassName={"page-item"}
          breakLinkClassName={"page-link"}
        />
      </div>
    </div>
  );
}

export default Films;
