import React, { useState, useEffect } from "react";
import Navbar from "../Components/Navbar";
import { Link } from "react-router-dom";

function Favorites() {
  const [data, setData] = useState([]);
  const [title, setTitle] = useState("");

  const checkData = () => {
    const getData = JSON.parse(localStorage.getItem("Data"));

    if (getData.length !== 0) {
      setData(getData);
      setTitle("My Favorites");
    } else {
      setTitle("No Favorites Yet");
    }
  };

  useEffect(() => {
    checkData();
  }, []);

  const removeItem = (e) => {
    const getData = JSON.parse(localStorage.getItem("Data"));

    const getItemId = e.target.parentNode.parentNode.getAttribute("id");

    const removeFilm = getData.filter((item) => {
      return item.id !== getItemId;
    });

    localStorage.setItem("Data", JSON.stringify(removeFilm));

    window.location.reload();
  };

  return (
    <>
      <Navbar />
      <div className="content">
        <h1>{title}</h1>
        <div className="favorites">
          {data?.map((item) => {
            return (
              <>
                <div id={item.id} key={item.id} className="fav">
                  <div className="img">
                    <Link to={`/filmpage/` + item.id}>
                      <img
                        src={"https://image.tmdb.org/t/p/w500" + item.image}
                        alt={item.name}
                      />
                    </Link>
                  </div>
                  <div className="fav-content">
                    <Link to={`/filmpage/` + item.id}>
                      <h1>{item.name}</h1>
                      <p>{item.description.substring(0, 250)}...</p>
                    </Link>
                    <button onClick={removeItem}>Remove</button>
                  </div>
                </div>
              </>
            );
          })}
        </div>
      </div>
    </>
  );
}

export default Favorites;
