import React, { useState, useEffect } from "react";
import { useParams } from "react-router-dom";
import Navbar from "../Components/Navbar";
import ReactPaginate from "react-paginate";
import { AiFillSecurityScan } from "react-icons/ai";
import { Link } from "react-router-dom";

function Genre() {
  const { genre } = useParams();

  const [data, setData] = useState([]);

  const fetchData = async () => {
    const response = await fetch(
      `https://api.themoviedb.org/3/discover/movie?api_key=${process.env.REACT_APP_API_KEY}&with_genres=${genre}`
    );
    const data = await response.json();
    setData(data.results);
    console.log(data);
  };

  useEffect(() => {
    fetchData();
  }, []);

  const fetchPagination = async (selected) => {
    const response = await fetch(
      `https://api.themoviedb.org/3/discover/movie?api_key=${process.env.REACT_APP_API_KEY}&page=${selected}&with_genres=${genre}`
    );
    const page = await response.json();
    return page.results;
  };

  const handlePageClick = async (click) => {
    console.log(click.selected);

    let selected = click.selected + 1;

    const fetchFromPagination = await fetchPagination(selected);

    setData(fetchFromPagination);
  };

  const [name, setName] = useState([]);

  const getGenreName = async () => {
    const getGenres = await fetch(
      `https://api.themoviedb.org/3/genre/movie/list?api_key=${process.env.REACT_APP_API_KEY}`
    );
    const name = await getGenres.json();
    setName(name.genres.filter((item) => item.id == genre));
  };

  console.log(name);

  useEffect(() => {
    getGenreName();
  }, []);

  const Check = () => {
    if (name.length == 0) {
      return "kot";
    } else {
      return name[0].name;
    }
  };

  return (
    <>
      <Navbar />
      <div className="content">
        <h1 className="genre-title">
          Genre: <Check />
        </h1>
        <div className="films">
          {data.map((item) => {
            return (
              <Link key={item.id} to={`/filmpage/` + item.id}>
                <div className="film">
                  <div className="film-img">
                    <img
                      src={"https://image.tmdb.org/t/p/w500" + item.poster_path}
                      alt="Poster"
                    />
                  </div>
                  <h1 className="title">{item.title}</h1>
                  <span>{item.vote_average}</span>
                </div>
              </Link>
            );
          })}
          <div className="pagi">
            <ReactPaginate
              breakLabel="..."
              nextLabel="next >"
              onPageChange={handlePageClick}
              pageRangeDisplayed={3}
              pageCount={450}
              previousLabel="< previous"
              renderOnZeroPageCount={null}
              containerClassName={"pagination"}
              pageClassName={"page-item"}
              pageLinkClassName={"page-link"}
              previousClassName={"page-item"}
              previousLinkClassName={"page-link"}
              nextClassName={"page-item"}
              nextLinkClassName={"page-link"}
              breakClassName={"page-item"}
              breakLinkClassName={"page-link"}
            />
          </div>
        </div>
      </div>
    </>
  );
}

export default Genre;
