import React, { useState, useEffect } from "react";
import Navbar from "../Components/Navbar";
import { useParams } from "react-router-dom";
import { Link } from "react-router-dom";

function Search() {
  const { name } = useParams();

  const [data, setData] = useState([]);

  const fetchData = async () => {
    const response = await fetch(
      `https://api.themoviedb.org/3/search/movie?api_key=${process.env.REACT_APP_API_KEY}&page=1&query=${name}`
    );
    const data = await response.json();
    setData(data.results);
    console.log(data);
  };

  useEffect(() => {
    fetchData();
  }, [name]);

  let filtered = data.filter((item) => item.poster_path !== null);

  const Check = () => {
    if (data.length == 0) {
      return "No results found";
    } else {
      return `Results for: ${name}`;
    }
  };

  return (
    <>
      <Navbar />
      <h1 className="search-title">
        <Check />
      </h1>
      <div className="content">
        <div className="films">
          {filtered.map((item) => {
            return (
              <Link key={item.id} to={`/filmpage/` + item.id}>
                <div className="film">
                  <div className="film-img">
                    <img
                      src={"https://image.tmdb.org/t/p/w500" + item.poster_path}
                      alt="Poster"
                    />
                  </div>
                  <h1 className="title">{item.title}</h1>
                  <span>{item.vote_average}</span>
                </div>
              </Link>
            );
          })}
        </div>
      </div>
    </>
  );
}

export default Search;
