import React from "react";
import Navbar from "../Components/Navbar";
import Films from "../Components/Films";
import Genres from "../Components/Genres";

function Home() {
  return (
    <>
      <Navbar />
      <div className="hero">
        <div className="hero-img"></div>
      </div>
      <div className="content">
        <div className="genre">
          <h1>Popular Movies</h1>
          <Genres />
        </div>
        <Films />
      </div>
    </>
  );
}

export default Home;
